import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { TextInput } from '@Green-Dot-Corporation/eureka-ui-form-elements';
// import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';

// Styles
import './styles/todo-list-header.scss';

class ToDoListHeader extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
    };

    render() {
        const { contextCls } = this.props;

        return (
            <div className={cx(this.baseCls, contextCls)}>
                <TextInput
                    placeholder="Add new TODO"
                    aria-label="Add new TODO"
                />
            </div>
        );
    }

    baseCls = 'todo-header';
}

export default ToDoListHeader;
