import React from 'react';
import PropTypes from 'prop-types';
import { Checkbox } from '@Green-Dot-Corporation/eureka-ui-form-elements';

const ToDoItem = (props) => {
    const { todo = {} } = props;

    return (
        <>
            <Checkbox isChecked={todo.completed} /> {todo.description}
        </>
    );
};

ToDoItem.propTypes = {
    todo: PropTypes.object,
};

export default ToDoItem;
