import { PureComponent } from 'react';

import { Tile } from '@Green-Dot-Corporation/eureka-ui-layouts';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import ToDoList from './components/todo/ToDoList';
import ToDoListHeader from './components/todo/ToDoListHeader';
import Bump from './components/bump/Bump';
import ToDoService from './services/ToDoService';

// Styles
import './styles/ui-module.scss';

const { SECONDARY } = VARIANTS;

export default class ToDoModule extends PureComponent {
    state = {};

    render() {
        const { todos } = this.state;

        return (
            <Tile
                headingLvl={1}
                headingText="DD Switch"
                contextCls="module-temlpate"
            >
                <ToDoListHeader />
                <ToDoList todos={todos} />
                <Button
                    text="Setup"
                    variant={SECONDARY}
                    onClick={this.handleSetUpBtnClick}
                />
                <br />
                <Router>
                    <Link to="/bump">Go to Bump Page</Link>
                    <br />
                    <Link to="/">Back</Link>
                    <Routes>
                        <Route path="/bump" element={<Bump />} />
                    </Routes>
                </Router>
            </Tile>
        );
    }

    async componentDidMount() {
        const todos = await this.fetchTODOList();
        this.setState({ todos });
    }

    handleSetUpBtnClick = () => {
        alert(123);
    };

    fetchTODOList = async () => {
        const todos = await ToDoService.getTodos('/demo/todos');

        return todos;
    };
}
